import { useState } from "react";
import "./works.scss";

export default function Works() {
  const [currentSlide, setCurrentSlide] = useState(0);
  const data = [
    {
      id: "1",
      icon: "./assets/globe.png",
      title: "Web Design",
      desc: "Explore the fusionary elegance of CSS, JavaScript, and HTML, redefining web development with power and creativity. ",
      img: "https://99designs-blog.imgix.net/blog/wp-content/uploads/2018/10/attachment_100040756-e1538485934255.jpeg?auto=format&q=60&fit=max&w=930",
    },
    {
      id: "2",
      icon: "./assets/globe.png",
      title: "Framework Used",
      desc: "Explore the fusionary elegance of React.js and SASS, redefining web development with power and creativity.",
      img: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcThm--WaKDR8ELk6lcs_zT43F_c43EAabstcAW9rYIZhrLQ2khv8GPjFxReFS-862J4P1A&usqp=CAU",
    },
    {
      id: "3",
      icon: "./assets/mobile.png",
      title: "Mobile dev",
      desc: "Discover the seamless innovation of React Native, powered by Expo, redefining mobile development with versatility and ingenuity.",
      img: "data:image/png;base64,",
    },
    {
      id: "3",
      title: "Node.js",
      icon: "./assets/mobile.png",
      desc: "Node.js is powerful due to its non-blocking, asynchronous architecture, enabling highly performant and scalable web applications.",
      img: "data:image/jpeg;base64,",
    },
  ];

  const handleClick = (way) => {
    way === "left"
      ? setCurrentSlide(currentSlide > 0 ? currentSlide - 1 : 2)
      : setCurrentSlide(currentSlide < data.length - 1 ? currentSlide + 1 : 0);
  };

  return (
    <div className="works" id="works">
      <div
        className="slider"
        style={{ transform: `translateX(-${currentSlide * 100}vw)` }}
      >
        {data.map((d) => (
          <div className="container">
            <div className="item">
              <div className="left">
                <div className="leftContainer">
                  <div className="imgContainer">
                    <img src={d.icon} alt="" />
                  </div>
                  <h2>{d.title}</h2>
                  <p>{d.desc}</p>
                  <span>Projects</span>
                </div>
              </div>
              <div className="right">
                <img src={d.img} alt="" />
              </div>
            </div>
          </div>
        ))}
      </div>
      <img
        src="assets/arrow.png"
        className="arrow left"
        alt=""
        onClick={() => handleClick("left")}
      />
      <img
        src="assets/arrow.png"
        className="arrow right"
        alt=""
        onClick={() => handleClick()}
      />
    </div>
  );
}
